import React from 'react'

export default function Search(props) {
    return (
        <div className="searchContainer">
            <form onSubmit={props.handleGetRequest}>
                <input
                    type="text"
                    autoComplete="off"
                    name="searchValue"
                    placeholder="search for city"
                />
                <button>Search</button>
            </form>
        </div>
    )


}
