import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { Link } from 'react-router-dom'
import './App.css'
import Navbar from './Components/Navbar'
import ForecastDetails from './Components/ForecastDetails'
import Search from './Components/Search'
import Favorites from './Components/Favorites'

export default function App(props) {
  const API_KEY = '8rDihLNZMdzkxHnLe8F0V3mjWmV7wA0v'
  let history = useHistory();

  const [currentTemp, setCurrentTemp] = useState('')
  const [currentText, setCurrentText] = useState('')
  const [forecast, setForecast] = useState([])
  const [city, setCity] = useState({ name: 'Tel-Aviv', url: `http://dataservice.accuweather.com/currentconditions/v1/215854?apikey=${API_KEY}`, key: 215854 });
  const [favorites, setFavorites] = useState([])
  const [isFav, setIsFav] = useState(false)


  useEffect(() => {
    if (!city) { setCity({ name: 'Tel-Aviv', url: `http://dataservice.accuweather.com/currentconditions/v1/215854?apikey=${API_KEY}`, key: 215854 }) }
    const fiveDayURL = `http://dataservice.accuweather.com/forecasts/v1/daily/5day/${city.key}?apikey=${API_KEY}&metric=true`
    async function getData() {
      const currentRequest = await fetch(city.url)
      const currentResponse = await currentRequest.json()
      if (currentResponse.Code === 'ServiceUnavailable') {
        console.log(currentResponse.Message)
        return <div>{currentResponse.Message}</div>
      }
      console.log(currentResponse);
      const weatherText = currentResponse[0].WeatherText
      const temp = currentResponse[0].Temperature.Metric.Value
      setCurrentText(weatherText)
      setCurrentTemp(temp)
      const forecastRequest = await fetch(fiveDayURL)
      const forecastResponse = await forecastRequest.json()
      console.log(forecastRequest);
      console.log(forecastResponse);
      setForecast(forecastResponse.DailyForecasts)

    }
    getData();
  }, [city])


  const handleGetRequest = async (e) => {
    e.preventDefault()
    const searchTerm = e.target.elements.searchValue.value
    const getCityURL = `http://dataservice.accuweather.com/locations/v1/cities/search?apikey=${API_KEY}&q=${searchTerm}`
    try {
      const request = await fetch(getCityURL)
      const response = await request.json()
      console.log(request);
      console.log(response);
      const arrCityDetails = [...response]
      const chosenCity = { name: arrCityDetails[0].EnglishName, key: arrCityDetails[0].Key, url: `http://dataservice.accuweather.com/currentconditions/v1/${arrCityDetails[0].Key}?apikey=${API_KEY}` }
      setCity(chosenCity)
      setIsFav(false)
    }
    catch (error) {
      console.log(error);
    }
  }

  const clickedCity = (key, favorites, name, url) => {
    setFavorites(favorites)
    setCity({ name: name, key: key, url: url })
    history.push('/')

  }

  const addToFav = (city) => {
    const newFav = city
    setFavorites([...favorites, newFav])
    setIsFav(true)
  }


  const removeFav = (key) => {

    setIsFav(false)
    setFavorites(favorites.filter((fav) => fav.key !== key))
  }

  const goHome = (city, favorites) => {
    setCity(city)
    setFavorites(favorites)
    history.push('/')
  }


  const formatForDays = (day) => {
    const findDay = (day) => {
      switch (day) {
        case 1:
          dayText = 'Mon'
          break;
        case 2:
          dayText = 'Tue'
          break;
        case 3:
          dayText = 'Wed'
          break;
        case 4:
          dayText = 'Thu'
          break;
        case 5:
          dayText = 'Fri'
          break;
        case 6:
          dayText = 'Sat'
          break;
        case 7:
          dayText = 'Sun'
          break;

        default:
          dayText = 'empty'
          break;
      }
    }
    let dayText = ''
    let daysArr = []
    const date = new Date()
    const i = date.getDay()
    for (let index = 0; index < i + 4 - index; index++) {
      if (index + i < 8) {
        const dayText = findDay(index + i);
      } else {
        const dayText = findDay(index + i - 7)
      }
      console.log(dayText);
      daysArr.push(dayText)

    }
    console.log(daysArr);
  }


  return (
    <div className="App">

      <div className="navbar">
        <Link to='/'>
          <button>Home</button>
        </Link>
        <Link to={{
          pathname: `/favorites`,
          state: { favorites }
        }}>
          <button className="nav-button" >Favorites</button>
        </Link>

      </div>
      <Search handleGetRequest={handleGetRequest} />
      <div className="mainContainer">
        <div>
          <h2>{city.name}</h2>
          <h3>{currentTemp}Â°c</h3>
        </div>
        <button className="addToBut" onClick={() => { !isFav ? addToFav(city) : removeFav(city.key) }}>{!isFav ? 'Add to Favorites' : 'remove from favorites'}</button>
      </div>
      <div>
        <h1 className="weatherText">{currentText}</h1>
        {forecast.map((forecast, index) => {
          return (
            <div className="forecastContainer">
              <p>{forecast.Date.slice(0, 10)}</p>
              <p>{forecast.Temperature.Minimum.Value}</p>
              <p>{forecast.Temperature.Maximum.Value}</p>
              <p>{forecast.Day.IconPhrase}</p>
              <p>{forecast.Night.IconPhrase}</p>
            </div>
          )
        })}
      </div>

    </div>
  );
}



