import React, { useState, useEffect } from 'react'
import { Link, useHistory } from 'react-router-dom'
import Navbar from './Navbar'
import { UseHistory } from 'react-router-dom'

export default function Favorites(props) {

    const [localFav, setLocalFav] = useState([])
    const [newCity, setNewCity] = useState({})
    const [tempFav, setTempFav] = useState({})
    let history = useHistory()

    console.log(props);

    useEffect(() => {
        props.location.state.favorites.map((favorite, index) => {
            async function fetchData() {
                const currentWeatherRequest = await fetch(favorite.url)
                const currentWeatherResponse = await currentWeatherRequest.json()
                console.log(currentWeatherResponse);
                const temp = currentWeatherResponse[0].Temperature.Metric.Value
                const fav = { name: favorite.name, url: favorite.url, key: favorite.key, temp: temp }
                console.log(temp);
                setLocalFav([...localFav, fav])
                console.log(localFav);


                if (currentWeatherResponse.Code === 'ServiceUnavailable') {
                    console.log(currentWeatherResponse.Message)
                    return <div>{currentWeatherResponse.Message}</div>
                }
            }
            fetchData()
            // setLocalFav(props.location.state.favorites)
        })
    }, [])




    return (
        <div>
            {localFav !== [] && <div>
                <Link to='/'>
                    <button onClick={() => props.goHome(localFav, newCity)} className="nav-button" city={newCity}>Home</button>
                </Link>
                <Link to='/favorites'><button>Favorites</button></Link>
                <div className="favorite">
                    {localFav.map((fav, index) => {
                        return (
                            <div onClick={() => { props.clickedCity(fav.key, localFav, fav.name, fav.url) }} className="favcontainer">
                                <h2>{fav.name}</h2>
                                <h4>{Math.round(localFav[index].temp)}</h4>
                                <button onClick={() => props.removeFav(fav.key)}>Remove</button>
                            </div>

                        )
                    })}
                </div>
            </div>
            }
        </div>
    )
}
